import React, {Component} from 'react';

class BitcoinPrice extends Component{
  constructor(props){
    super(props);
    this.state = {
      currency: 'USD'
    };
  }
  render(){
    let price_desc;
    if(this.state.currency === 'USD') {
      price_desc = <li className="list-group-item">
      Bitcoin rate for {this.props.bpi.USD.description}: <span class="badge badge-primary">{this.props.bpi.USD.code}</span> <strong>{this.props.bpi.USD.rate}</strong></li>
    } else if(this.state.currency === 'GBP') {
      price_desc = <li className="list-group-item">
      Bitcoin rate for {this.props.bpi.GBP.description}: <span class="badge badge-warning">{this.props.bpi.GBP.code}</span> <strong>{this.props.bpi.GBP.rate}</strong></li>
    } else if(this.state.currency === 'EUR') {
      price_desc = <li className="list-group-item">
      Bitcoin rate for {this.props.bpi.EUR.description}: <span class="badge badge-info">{this.props.bpi.EUR.code}</span> <strong>{this.props.bpi.EUR.rate}</strong></li>
}
    return (
      <div>
      <ul className="list-group">
        {price_desc}
      </ul>
      <br />
        <select onChange={e => this.setState({currency: e.target.value})} className="form-control">
          <option value="USD">USD</option>
          <option value="GBP">GBP</option>
          <option value="EUR">EUR</option>
        </select>
      </div>
    );
  }
}

export default BitcoinPrice;



